	var _self , logRes;
	import listCell from '@/components/mix-list-cell';
      
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell
		},
		data(){
			return {
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				user:[]
			}
		},
		onLoad(){
			
			_self = this;
			logRes = _self.checkLogin('../my/my',2);
			
			//console.log(logRes[0])
			uni.request({
				url: _self.apiServer+'mall/user/findByUserid?userid='+logRes[0],
				method: 'GET',
				data: {},
				success: res => {
					if(res.data.status == 200){
						
						console.log(res.data.data);
						_self.user = res.data.data;
					}
					
				},
				fail: (e) => {
					
					uni.showToast({
						title:e.data.msg,
						icon:"none"
					})
				},
				complete: () => {}
			});
			
			
		},
		
       /* computed: {
			...mapState(['hasLogin','userInfo'])
		}, */
        methods: {
			//跳转到获得积分界面
			toGetIntegral:function(){
				
				uni.navigateTo({url: 'getIntegral'});
			},
			toOrder(){
				uni.navigateTo({
					url:'../order/order'
				})
			},
			toComplaint(){
				uni.switchTab({
					url: '../complaint/complaint'
				})
			},
			toModify(){
				var users = JSON.stringify(this.user);
				uni.navigateTo({url: 'modify?user='+users});
			},
			toSet(){
				uni.navigateTo({
					url:'/pages/set/set'
				})
			}
        }  
    }  
